body.thank-you{
  background: white url('../../images/thank-you-bg.png') center center no-repeat;

  @include r(tablet){
    max-height: 100vh;
    overflow: hidden;
  }
}

.page-thank-you{
  min-height: 100vh;
  background: transparent url('../../images/404-menu-bg.png') top right no-repeat;
  background-size: 400px auto;

  @include r(tablet){
    background-size: 800px auto;
  }

  @include r(desktop) {
    background-size: 1055px auto;
  }

  .container {

    @include media-breakpoint-down(sm) {
      padding: 0;
    }

  }

  .thank-you-wrapper{
    //padding: 140px 0px;
    text-align: center;
    box-sizing: border-box;
    position: fixed;
    width: 768px;

    @include media-breakpoint-down(sm) {
      position: relative;
      width: auto;
      padding: 0 20px;
    }

    .thank-you-image{
      display: flex;
      align-items: center;
      justify-content: center;
      height: 700px;
      padding: 0 30px;

      @include media-breakpoint-down(sm) {
        padding: 0;
      }

     /* @include r(tablet){
        display: flex;
        align-items: center;
        justify-content: center;
        min-height: 700px;
      }

      @include r(tablet){
        min-height: 700px;
      }*/

      &:after{
        background: transparent url('../../images/thank-you-image.png') center center no-repeat;
        background-size: cover;
        width: 100%;
        height: 640px;
        max-width: 800px;
        position: absolute;
        top: 340px;
        margin-bottom: -170px;
        left: 50%;
        transform: translate(-50%,-50%);
        content: '';
        display: block;

        @include r(tablet){
          background-size: contain;
        }

        @include r(desktop){
          max-width: 1050px;
        }

        @include media-breakpoint-down(sm) {
          top: 370px;
          height: 588px;
        }

      }

      .thank-you-image-wrapper{
        max-width: 544px;
        //margin: 0 auto;
        position: relative;
        z-index: 2;
        color: #ffffff;

        .checkmark{
          margin-bottom: 20px;
        }

        h1{
          margin: 0;
          font-size: 45px;
          font-weight: 900;
          text-shadow: 0 4px 6px rgba(0, 0, 0, 0.15);
          margin-bottom: 20px;

          @include media-breakpoint-down(sm) {
            font-size: 25px;
          }

        }

        p{
          font-size: 25px;
          font-weight: 300;
          line-height: 36px;

          @include media-breakpoint-down(sm) {
            font-size: 17px;
            line-height: 24px;
          }

        }

        a {
          color: inherit;
        }
      }
    }
  }
}
